.container-social-media {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    display: inline;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
    text-align: center;


}

.icons a img{
    width: 50px;
    height: 50px;
    margin: 0 10px;
    transition: all 0.3s;
}

.icons img:hover{
    transform: scale(1.2);
}